@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,400&display=swap');

:root {
  --font-family: 'IBM Plex Mono', sans-serif;

  --color-bg: #fff;
  --color-two: #f1f1f1;
  --color-text: #000;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

a {
  color: var(--color-text);
  font-family: var(--font-family);
  text-decoration: none;
}

p, span, h1, h2, h3 {
  color: var(--color-text);
  font-family: var(--font-family);
}